$packery-width: calc(100% - 350px);
body {
	height: 100%;
	height: 100vh;
	width: 100%;
	width: 100vw;
	overflow: hidden;
	background-color: #f1f1f1;
}
.wrapper {
	background-color: #f1f1f1;
	width: $packery-width;
	float:right;
	display: inline-block;
	height: 100%; //fallback
	height: 100vh;
	overflow: hidden;
	box-sizing: border-box;
	padding: 10px; //matches packery gutter
}

.tile-title {
	transition: all .3s ease-in-out;
	opacity: 0;
	color: white;
	text-transform: uppercase;
	letter-spacing: 2px;
	position: relative;
	line-height: 20px;
	top: 0;
	text-align: center;
}

#container {
	height: 100% !important;
}

.item {
	width: calc(50% - 7.5px);
	// min-width: 550px;
	// height: 200px;
	height: calc(50% - 7.5px);
	overflow: hidden;
	// height: 338px;
	background-image: linear-gradient(to bottom, rgba(#ff7c00, 0) 0%, rgba(#ff7c00, 0) 50%, rgba(#ffa73d, .7) 50%, rgba(#ffc27c, .7) 100%);
	background-color: #ff7c00;
	background-size: 100% 200%;
	background-position: top center;
	// transition: width 100ms linear, height 100ms linear 150ms, transform 0ms linear 100ms;
	transform: translate3d(0, 0, 0);
	&:hover {
		background-position: bottom center;
	}
	&.full-screen {
		transform: translate3d(0, 0, 200px);
		width: 100%;
		height: 100%;
